*, *::before, *::after {
    box-sizing: border-box;
}

* {
    margin: 0;
    padding: 0;
    font: inherit;
}

body {
    min-height: 100vh;
}

img, picture, svg, video {
    display: block;
    max-width: 100%;
}

/* --- Fonts & Custom Properties --- */
:root {
    --dark-mode-bg-color: hsl(0, 0%, 12%);
    --main-font-color: hsl(0, 0%, 100%);
    --secondary-font-color: hsl(0, 0%, 45%);
}

@font-face {
    font-family: "heading-font";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    font-display: swap;
}

@font-face {
    font-family: "body-font";
    src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
    font-display: swap;
}

/* --- Base --- */
body {
    display: grid;
    grid-template-rows: 0.25fr 0.1fr 0.1fr minmax(270px, 1fr) 0.7fr 0.3fr 0.1fr;
    /* grid-template-columns: repeat(autofill, minmax(250px, 2fr)); */
    row-gap: 10px;
    background-color: var(--dark-mode-bg-color);
}

h1, h2, h3 {
    color: var(--main-font-color);
    font-family: "heading-font";
}

p, a, input, img, label, button {
    color: var(--main-font-color);
    font-family: "body-font";
}

/* 1) Header */
header {
    grid-row: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0.25rem 1rem;
}

header h1 {
    font-family: "heading-font";
    font-size: 3.5rem;
}

/* 2) Search */
.searchbox-wrapper {
    grid-row: 2;
    display: flex;
    justify-content: center;
}

.searchbox {
    width: 80%;
    height: 40px;
    background-color: hsl(0, 0%, 16%);
    border-radius: 20px;
    position: relative;
    z-index: 1;
}

.searchbox > input {
    position: absolute;
    top: 0;
    left: 0;
    border: none;
    padding: 0 20px;
    width: 80%;
    height: 40px;
    background-color: hsl(0, 0%, 16%);
    border-radius: 20px;
    z-index: 10;
    caret-color: rgb(248, 144, 96);
}

.searchbox > input:focus {
    outline: none;
}

.searchbox > input::placeholder {
    color: var(--secondary-font-color);
    font-family: "body-font";
    font-size: 1.2rem;
}

.searchbox > .search-btn {
    display: grid;
    place-items: center;
    font-family: "body-font";
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 20%;
    border: none;
    border-radius: 20px;
    color: var(--main-font-color);
    background: linear-gradient(to right, rgb(248, 144, 96), rgb(255,55,53));
    z-index: 100;
    cursor: pointer;
}

/* Location */
.location-wrapper {
    grid-row: 3;
    display: flex;
    justify-content: center;
}

.location-box {
    width: 90%;
    padding: 0 1rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

#location-pin {
    height: 1rem;
    width: 1rem;
}

.timezone-wrapper {
    display: none;
}

/* 3) Weather Main */
.weather-wrapper {
    grid-row: 4;
    display: flex;
    justify-content: center;
    align-items: center;
}

.weather-panel {
    display: grid;
    grid-template-columns: repeat(3, 33.33%);
    grid-template-rows: 25% 35% 40%;
    width: 90%;
    height: 100%;
    padding: 1rem;
    border-radius: 25px;

    /* Glass */
    background: rgba(255, 255, 255, 0.15);
    backdrop-filter: blur(14.5px);
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);
    -webkit-backdrop-filter: blur(14.5px);
}

.description-box {
    grid-row: span 2;
    grid-column: 1;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

#day {
    font-size: 2rem;
}

#description {
    font-size: 1rem;
}

.degrees, #temperature {
    font-size: 5.5rem;
}

.temp-box {
    grid-column: 2;
    grid-row: 1 / 3;
    font-size: 6rem;

    display: flex;
    justify-content: center;
    align-items: center;
}

.icon-box {
    grid-column: 3;
    grid-row: 1 / 3;

    display: flex;
    justify-content: center;
    align-items: center;
}

#icon {
    width: 110px;
    height: 110px;
}

.calendar-box {
    grid-row: 3;
    grid-column: span 3;

    display: flex;
    place-items: center;
}

.day-box, .timeframe-box {
    height: 100%;
    width: 70%;
    background-color: hsl(0, 0%, 16%);
    border-radius: 20px;
    margin: 0 0.3rem;
    padding-bottom: 2rem 0;
    display: grid;
    place-items: center;
}

.day-weather-icon {
    width: 50px;
}

/* Details */
.details-wrapper {
    grid-row: 5;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.details-panel {
    display: grid;
    place-items: center;
    grid-template-columns: repeat(3, 33%);
    grid-template-rows: repeat(2, 50%);
    width: 90%;
    height: 100%;
    padding: 1rem;
    border-radius: 25px;
    background-color: rgb(40,40,40);
}

.details-box {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.details-box > p:first-of-type,
.details-box > p > span {
    color: var(--main-font-color);
}

.details-box > p:last-child {
    color: var(--secondary-font-color);
}

.timeframe-controls {
    display: none;
}

.info {
    width: 15px;
    fill: var(--secondary-font-color);
}

.rainfall-wrapper, .timeframe-box{
    display: none;
}

/* 4) Astrology */
.sun-astrology-wrapper {
    grid-row: 6;
    display: grid;
    place-items: center;
}

.sun-visual-panel {
    display: none;
}

.moon-astrology-wrapper {
    grid-row: 7;
    display: grid;
    place-items: center;
}

.astrology-panel {
    display: flex;
    justify-content: space-between;
    width: 90%;
    border-radius: 25px;
}

.sunrise-box, .sunset-box, .astrology-box {
    display: grid;
    place-items: center;
    background-color: rgb(40,40,40);
    width: 45%;
    padding: 0.7rem;
    border-radius: 20px;
}

.sunrise-box > p:first-child,
.sunset-box > p:first-child,
.astrology-box > p:first-child {
    color: var(--secondary-font-color);
}

.sunrise-box > p:last-child,
.sunset-box > p:last-child, 
.astrology-box > p:last-child {
    font-size: 1.1rem;
}

.sun-visual-wrapper > *,
.sun {
    display: none;
}

.moon-astrology-visual > *,
.moon {
    display: none;
}

/* 5) Footer */
footer {
    grid-row: -1;
    grid-column: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.fa-square-github,
.fa-square-github:active,
.fa-square-github:visited, 
a {
    color: white;
    text-decoration: none;
}

/* Media Queries */
@media (min-width: 780px) {
    body {
        grid-template-rows: 0.3fr 0.1fr 0.1fr 2fr 2fr 0.1fr;
        grid-template-columns: 1fr 1fr;
