    }

    #description {
        font-size: 1.2rem;
    }

    .degrees, #temperature {
        font-size: 7rem;
        padding-left: 2rem;
    }

    .temp-box {
        /* grid-row: 2 / -1;
        grid-column: 1 / 3; */

        grid-row: 2 / -1;
