    }

    .timeframe-panel {
        display: grid;
        place-items: center;
        grid-template-rows: 1fr 5fr;
        grid-template-columns: repeat(4, 1fr);
        grid-auto-columns: 1fr;
        background-color: rgb(40,40,40);
        width: 90%;
        padding: 0.5rem;
        border-radius: 20px;
    }

    .timeframe-controls {
        grid-row: 1;
        grid-column: 2 / 4;
