        display: flex;
        flex-direction: column-reverse;
        justify-content: space-between;
        align-items: center;
        height: 90%;
    }

    input[type=range] {
        appearance: slider-vertical;
        -webkit-appearance: slider-vertical;
        width: 100%;
        height: 90%;
    }

    .rainfall-hour, .rainfall-data {
        margin: 0 auto;
    }

    #rainfall-hour-one {
        grid-row: 3;
