    #rainfall-hour-four {
        grid-row: 3;
        grid-column: 5;
    }

    #rainfall-data-four {
        grid-row: 2;
        grid-column: 5;
    }

    #rainfall-hour-five {
        grid-row: 3;
        grid-column: 6;
    }

    #rainfall-data-five {
        grid-row: 2;
        grid-column: 6;
    }

    #rainfall-hour-six {
        grid-row: 3;
        grid-column: 7;
    }

    #rainfall-data-six {
        grid-row: 2;
        grid-column: 7;
    }

    .sun-visual-wrapper {
        grid-row: 7;
