AEA;QACI,WAAW;QACX,cAAc;QACd,aAAa;QACb,mBAAmB;IACvB;;IAEA;QACI,aAAa;QACb,2CAA2C;QAC3C,oCAAoC;QACpC,+BAA+B;QAC/B,mBAAmB;QACnB,YAAY;QACZ,UAAU;QACV,qCAAqC;IACzC;;IAEA;QACI,WAAW;QACX,mBAAmB;IACvB;;IAEA;QACI,WAAW;QACX,mBAAmB;QACnB,cAAc;QACd,uBAAuB;IAC3B;;IAEA;QACI,WAAW;QACX,cAAc;QACd,aAAa;QACb,8BAA8B;QAC9B,8BAA8B;QAC9B,mBAAmB;QACnB,WAAW;IACf;;IAEA;QACI,2BAA2B;QAC3B,mCAAmC;QACnC,WAAW;QACX,WAAW;IACf;;IAEA;QACI,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,cAAc;;QAEd,aAAa;QACb,mBAAmB;IACvB;;IAEA;QACI,aAAa;QACb,8BAA8B;QAC9B,UAAU;QACV,mBAAmB;IACvB;;IAEA;QACI,aAAa;QACb,mBAAmB;QACnB,UAAU;QACV,eAAe;QACf,mBAAmB;IACvB;;IAEA;QACI,cAAc;QACd,WAAW;QACX,YAAY;IAChB;;IAEA;QACI,WAAW;QACX,cAAc;IAClB;;IAEA;QACI,aAAa;QACb,8BAA8B;QAC9B,UAAU;QACV,mBAAmB;IACvB;;IAEA;QACI,aAAa;QACb,mBAAmB;QACnB,UAAU;QACV,eAAe;QACf,mBAAmB;IACvB;;IAEA;QACI,cAAc;QACd,WAAW;QACX,YAAY;IAChB;;IAEA;QACI,YAAY;IAChB;;IAEA;QACI,YAAY;QACZ,mBAAmB;IACvB;AACJ;;AAEA;IACI;QACI,cAAc;IAClB;;IAEA;QACI,aAAa;IACjB;;IAEA;QACI,kCAAkC;IACtC;;IAEA;QACI,YAAY;IAChB;AACJ;;AAEA;;;;;;;;;;;;;;;;;;;;;GAqBG;;AAEH;IACI,aAAa;AACjB","sourcesContent":["/* --- Reset --- */\n*, *::before, *::after {\n    box-sizing: border-box;\n}\n\n* {\n    margin: 0;\n    padding: 0;\n    font: inherit;\n}\n\nbody {\n    min-height: 100vh;\n}\n\nimg, picture, svg, video {\n    display: block;\n    max-width: 100%;\n}\n\n/* --- Fonts & Custom Properties --- */\n:root {\n    --dark-mode-bg-color: hsl(0, 0%, 12%);\n    --main-font-color: hsl(0, 0%, 100%);\n    --secondary-font-color: hsl(0, 0%, 45%);\n}\n\n@font-face {\n    font-family: \"heading-font\";\n    src: url(\"./assets/fonts/Afacad-Bold.ttf\");\n    font-display: swap;\n}\n\n@font-face {\n    font-family: \"body-font\";\n    src: url(\"./assets/fonts/Afacad-VariableFont_wght.ttf\");\n    font-display: swap;\n}\n\n/* --- Base --- */\nbody {\n    display: grid;\n    grid-template-rows: 0.25fr 0.1fr 0.1fr minmax(270px, 1fr) 0.7fr 0.3fr 0.1fr;\n    /* grid-template-columns: repeat(autofill, minmax(250px, 2fr)); */\n    row-gap: 10px;\n    background-color: var(--dark-mode-bg-color);\n}\n\nh1, h2, h3 {\n    color: var(--main-font-color);\n    font-family: \"heading-font\";\n}\n\np, a, input, img, label, button {\n    color: var(--main-font-color);\n    font-family: \"body-font\";\n}\n\n/* 1) Header */\nheader {\n    grid-row: 1;\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n    margin: 0.25rem 1rem;\n}\n\nheader h1 {\n    font-family: \"heading-font\";\n    font-size: 3.5rem;\n}\n\n/* 2) Search */\n.searchbox-wrapper {\n    grid-row: 2;\n    display: flex;\n    justify-content: center;\n}\n\n.searchbox {\n    width: 80%;\n    height: 40px;\n    background-color: hsl(0, 0%, 16%);\n    border-radius: 20px;\n    position: relative;\n    z-index: 1;\n}\n\n.searchbox > input {\n    position: absolute;\n    top: 0;\n    left: 0;\n    border: none;\n    padding: 0 20px;\n    width: 80%;\n    height: 40px;\n    background-color: hsl(0, 0%, 16%);\n    border-radius: 20px;\n    z-index: 10;\n    caret-color: rgb(248, 144, 96);\n}\n\n.searchbox > input:focus {\n    outline: none;\n}\n\n.searchbox > input::placeholder {\n    color: var(--secondary-font-color);\n    font-family: \"body-font\";\n    font-size: 1.2rem;\n}\n\n.searchbox > .search-btn {\n    display: grid;\n    place-items: center;\n    font-family: \"body-font\";\n    position: absolute;\n    top: 0;\n    right: 0;\n    height: 100%;\n    width: 20%;\n    border: none;\n    border-radius: 20px;\n    color: var(--main-font-color);\n    background: linear-gradient(to right, rgb(248, 144, 96), rgb(255,55,53));\n    z-index: 100;\n    cursor: pointer;\n}\n\n/* Location */\n.location-wrapper {\n    grid-row: 3;\n    display: flex;\n    justify-content: center;\n}\n\n.location-box {\n    width: 90%;\n    padding: 0 1rem;\n    display: flex;\n    justify-content: flex-start;\n    align-items: center;\n}\n\n#location-pin {\n    height: 1rem;\n    width: 1rem;\n}\n\n.timezone-wrapper {\n    display: none;\n}\n\n/* 3) Weather Main */\n.weather-wrapper {\n    grid-row: 4;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.weather-panel {\n    display: grid;\n    grid-template-columns: repeat(3, 33.33%);\n    grid-template-rows: 25% 35% 40%;\n    width: 90%;\n    height: 100%;\n    padding: 1rem;\n    border-radius: 25px;\n\n    /* Glass */\n    background: rgba(255, 255, 255, 0.15);\n    backdrop-filter: blur(14.5px);\n    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);\n    -webkit-backdrop-filter: blur(14.5px);\n}\n\n.description-box {\n    grid-row: span 2;\n    grid-column: 1;\n\n    display: flex;\n    flex-direction: column;\n    justify-content: flex-start;\n    align-items: center;\n}\n\n#day {\n    font-size: 2rem;\n}\n\n#description {\n    font-size: 1rem;\n}\n\n.degrees, #temperature {\n    font-size: 5.5rem;\n}\n\n.temp-box {\n    grid-column: 2;\n    grid-row: 1 / 3;\n    font-size: 6rem;\n\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.icon-box {\n    grid-column: 3;\n    grid-row: 1 / 3;\n\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n#icon {\n    width: 110px;\n    height: 110px;\n}\n\n.calendar-box {\n    grid-row: 3;\n    grid-column: span 3;\n\n    display: flex;\n    place-items: center;\n}\n\n.day-box, .timeframe-box {\n    height: 100%;\n    width: 70%;\n    background-color: hsl(0, 0%, 16%);\n    border-radius: 20px;\n    margin: 0 0.3rem;\n    padding-bottom: 2rem 0;\n    display: grid;\n    place-items: center;\n}\n\n.day-weather-icon {\n    width: 50px;\n}\n\n/* Details */\n.details-wrapper {\n    grid-row: 5;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n}\n\n.details-panel {\n    display: grid;\n    place-items: center;\n    grid-template-columns: repeat(3, 33%);\n    grid-template-rows: repeat(2, 50%);\n    width: 90%;\n    height: 100%;\n    padding: 1rem;\n    border-radius: 25px;\n    background-color: rgb(40,40,40);\n}\n\n.details-box {\n    display: flex;\n    flex-direction: column;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n}\n\n.details-box > p:first-of-type,\n.details-box > p > span {\n    color: var(--main-font-color);\n}\n\n.details-box > p:last-child {\n    color: var(--secondary-font-color);\n}\n\n.timeframe-controls {\n    display: none;\n}\n\n.info {\n    width: 15px;\n    fill: var(--secondary-font-color);\n}\n\n.rainfall-wrapper, .timeframe-box{\n    display: none;\n}\n\n/* 4) Astrology */\n.sun-astrology-wrapper {\n    grid-row: 6;\n    display: grid;\n    place-items: center;\n}\n\n.sun-visual-panel {\n    display: none;\n}\n\n.moon-astrology-wrapper {\n    grid-row: 7;\n    display: grid;\n    place-items: center;\n}\n\n.astrology-panel {\n    display: flex;\n    justify-content: space-between;\n    width: 90%;\n    border-radius: 25px;\n}\n\n.sunrise-box, .sunset-box, .astrology-box {\n    display: grid;\n    place-items: center;\n    background-color: rgb(40,40,40);\n    width: 45%;\n    padding: 0.7rem;\n    border-radius: 20px;\n}\n\n.sunrise-box > p:first-child,\n.sunset-box > p:first-child,\n.astrology-box > p:first-child {\n    color: var(--secondary-font-color);\n}\n\n.sunrise-box > p:last-child,\n.sunset-box > p:last-child, \n.astrology-box > p:last-child {\n    font-size: 1.1rem;\n}\n\n.sun-visual-wrapper > *,\n.sun {\n    display: none;\n}\n\n.moon-astrology-visual > *,\n.moon {\n    display: none;\n}\n\n/* 5) Footer */\nfooter {\n    grid-row: -1;\n    grid-column: 1;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n}\n\n.fa-square-github,\n.fa-square-github:active,\n.fa-square-github:visited, \na {\n    color: white;\n    text-decoration: none;\n}\n\n/* Media Queries */\n@media (min-width: 780px) {\n    body {\n        grid-template-rows: 0.3fr 0.1fr 0.1fr 2fr 2fr 0.1fr;\n        grid-template-columns: 1fr 1fr;\n        row-gap: 1.5rem;\n        margin: 0 2rem;\n    }\n\n    header > h1 {\n        font-size: 5rem;\n    }\n\n    .searchbox-wrapper {\n        grid-column: span 2;\n    }\n\n    #city, #region {\n        font-size: 1.1rem;\n    }\n\n    .timezone-wrapper {\n        grid-row: 3;\n        grid-column: 2;\n        display: flex;\n        justify-content: center;\n    }\n    \n    .timezone-box {\n        display: flex;\n        justify-content: flex-end;\n        align-items: flex-end;\n        width: 90%;\n        padding: 0 1rem;\n    }\n\n    .day-box, .calendar-box {\n        display: none;\n    }\n\n    .description-box {\n        grid-row: 1;\n        grid-column: 2;\n    }\n\n    #day {\n        font-size: 3rem;\n    }\n\n    #description {\n        font-size: 1.2rem;\n    }\n\n    .degrees, #temperature {\n        font-size: 7rem;\n        padding-left: 2rem;\n    }\n\n    .temp-box {\n        /* grid-row: 2 / -1;\n        grid-column: 1 / 3; */\n\n        grid-row: 2 / -1;\n        grid-column: 2 / -1;\n    }\n\n    .icon-box {\n        /* grid-row: 1 / -1;\n        grid-column: 3; */\n\n        grid-row: 2 / -1;\n        grid-column: 1 / 3;\n\n    }\n\n    .details-wrapper {\n        grid-row: 4;\n        grid-column: 2;\n    }\n\n    .timeframe-wrapper {\n        display: flex;\n        justify-content: center;\n        grid-row: 5;\n        grid-column: 1;\n    }\n\n    .timeframe-panel {\n        display: grid;\n        place-items: center;\n        grid-template-rows: 1fr 5fr;\n        grid-template-columns: repeat(4, 1fr);\n        grid-auto-columns: 1fr;\n        background-color: rgb(40,40,40);\n        width: 90%;\n        padding: 0.5rem;\n        border-radius: 20px;\n    }\n\n    .timeframe-controls {\n        grid-row: 1;\n        grid-column: 2 / 4;\n        background-color: grey;\n        width: 100%;\n        height: 80% ;\n        border-radius: 20px;\n        display: flex;\n        justify-content: space-evenly;\n    }\n\n    .timeframe-controls > button {\n        border: none;\n        width: 50%;\n        border-radius: 20px;\n        background-color: hsl(0, 0%, 50%, 0.392);\n        color: black;\n        cursor: pointer;\n    }\n    \n    .active {\n        background: linear-gradient(to right, rgb(248, 144, 96), rgb(255,55,53));\n    }\n\n    .daily-slots, .hourly-slots {\n        grid-row: 2;\n        grid-column: 1 / -1;\n        display: flex;\n        height: 90%;\n        width: 100%;\n    }\n\n    .hourly-slots {\n        overflow: scroll;\n    }\n\n    .daily-box-one,\n    .daily-box-two,\n    .daily-box-three,\n    .daily-box-four, \n    .hourly-box-one, \n    .hourly-box-two, \n    .hourly-box-three,\n    .hourly-box-four,\n    .hourly-box-five,\n    .hourly-box-six {\n        display: grid;\n        background-color: hsl(0, 0%, 50%, 0.392);\n        margin: 0 0.3rem;\n    }\n\n    .rainfall-wrapper {\n        grid-row: 5;\n        grid-column: 2;\n        display: grid;\n        place-items: center;\n    }\n\n    .rainfall-panel {\n        display: grid;\n        grid-template-columns: 0.5fr repeat(6, 1fr);\n        grid-template-rows: 0.3fr auto 0.3fr;\n        background-color: rgb(40,40,40);\n        border-radius: 20px;\n        height: 100%;\n        width: 90%;\n        padding: 0.75rem 0.5rem 0.5rem 0.5rem;\n    }\n\n    .rainfall-hours {\n        grid-row: 2;\n        grid-column: 2 / -1;\n    }\n\n    .rainfall-title-box {\n        grid-row: 1;\n        grid-column: 1 / -1;\n        margin: 0 auto;\n        padding-bottom: 0.2rem ;\n    }\n\n    .percentage-range-box {\n        grid-row: 2;\n        grid-column: 1;\n        display: flex;\n        